* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif;
}

.hawaii html {
  scroll-behavior: smooth;
}
.hawaii h1,
.hawaii h2,
.hawaii h3,
.hawaii h4,
.hawaii h5,
.hawaii h6 {
  font-weight: normal;
}
.hawaii header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  position: sticky;
  top: 0px;
  background-color: white;
  z-index: 1;
  transition: all 0.5s;
}
.hawaii header div.logo {
  margin-left: 2rem;
  display: flex;
  align-items: center;
  flex: 5 1 400px;
}
.hawaii header div.logo img {
  width: 6vw;
  height: 6vw;
}
.hawaii header nav {
  flex: 2 1 400px;
}
.hawaii header nav ul {
  display: flex;
  list-style-type: none;
  justify-content: space-around;
}
.hawaii header nav ul li button {
  color: #0f9e9e;
  text-decoration: none;
  font-size: 1.35rem;
  background: none;
  border: none;
  padding: 0;
}
.hawaii main {
  display: flex;
  flex-direction: column;
  background-color: white;
}
.hawaii main section.backImage {
  min-height: 90vh;
  width: 100%;
  background-image: url("../Pictures/pexels-photo-416676.jpeg");
  background-size: cover;
  background-position: center;
}
.hawaii main section.backImage div.filter {
  background-color: rgba(0, 0, 0, 0.35);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.hawaii main section.backImage div.filter h3 {
  color: white;
  font-size: 3.5rem;
  margin: 2rem 0rem;
}
.hawaii main section.backImage div.filter button.start {
  background-color: #00e7cb;
  padding: 0.75rem 1.5rem;
  font-size: 1.5rem;
  color: white;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
}
.hawaii main section.second {
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  justify-content: space-around;
}
.hawaii main section.second .theme {
  color: #0f9e9e;
}
.hawaii main section.second h2 {
  font-size: 2rem;
}
.hawaii main section.second section.cards {
  display: flex;
  width: 80%;
  min-height: 75vh;
  flex-wrap: wrap;
}
.hawaii main section.second section.cards div.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  flex: 1 1 300px;
}
.hawaii main section.second section.cards div.card div.image-container {
  overflow: hidden;
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.hawaii main section.second section.cards div.card div.image-container img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  transition: all 0.2s;
}
.hawaii main section.second section.cards div.card div.image-container img:hover {
  transform: scale(1.2);
}
.hawaii main section.second section.cards div.card h4 {
  margin: 0.5rem 0rem;
  font-size: 2rem;
}
.hawaii main section.second section.cards div.card p {
  margin: 0.5rem 0rem;
  font-size: 1.25rem;
}
.hawaii main section.second section.cards div.card button {
  margin: 0.5rem 0rem;
  text-decoration: none;
  padding: 1rem 1.5rem;
  background-color: #0b7575;
  color: white;
  font-size: 1.25rem;
  border: none;
}
.hawaii main footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #d3d3d3;
  min-height: 30vh;
  justify-content: space-around;
}
.hawaii main footer div.logo {
  display: flex;
  align-items: center;
}
.hawaii main footer div.logo img {
  width: 60px;
  height: 60px;
}
.hawaii main footer nav {
  width: 20%;
}
.hawaii main footer nav ul {
  display: flex;
  list-style-type: none;
  justify-content: space-around;
}
.hawaii main footer nav ul li {
  margin: 0rem 1rem;
}
.hawaii main footer nav ul li a {
  color: #6f6e6e;
  text-decoration: none;
  font-size: 1.35rem;
}
.hawaii main footer section.links a {
  margin: 0rem 1rem;
}
.hawaii main footer section.links a img {
  width: 50px;
  height: 50px;
}
@media screen and (max-width: 550px) {
  .hawaii header div.logo {
    margin-left: 0.5rem;
  }
  .hawaii header div.logo img {
    width: 10vw;
    height: 10vw;
  }
}/*# sourceMappingURL=style.css.map */